import React, { useState, useContext } from 'react';
import GithubContext from '../../context/github/GithubContext';
import AlertContext from '../../context/alert/AlertContext';

const Search = () => {
	const githubContext = useContext(GithubContext);
	const alertContext = useContext(AlertContext);
	
	const [text, setText] = useState('');
	
	//When query is typed into search bar update state.text
	const onChange = (e) => setText(e.target.value);
	
	/* Prevent page reload and set props equal to the search query 
	when submit button is clicked */ 
	const onSubmit = e => {
		e.preventDefault();
		if(text === '') {
			alertContext.setAlert('Please enter something', 'light');
		} else {
			githubContext.searchUsers(text);
			setText('');
		}
	};
	
	//Create search form 	
		return (
			<div>
				
				<form onSubmit={onSubmit} className="form">
					<input type="text" name="text" 
					placeholder="Search users..." value={ text }
					onChange={onChange} autoFocus/>
					<input type="submit" name="search" className="btn btn-dark btn-block" value="Search"/>
					
				</form>
				{ githubContext.users.length > 0 && (
					<button 
						className="btn btn-light btn-block"
						onClick={ githubContext.clearUsers }
					>
						Clear
					</button>
				)}
			</div>
		);
}

export default Search