import React, { useEffect, Fragment, useContext } from 'react';
import Spinner from '../layout/Spinner';
import Repos from '../repos/Repos';
import { Link } from 'react-router-dom'; 
import GithubContext from '../../context/github/GithubContext';

const User = ({ match }) => {
	const githubContext = useContext(GithubContext);
	
	const { user, loading, getUser, repos, getUserRepos } = githubContext;
	
	useEffect(() => {
		//gets params from url
		getUser(match.params.login);
		getUserRepos(match.params.login);
		// eslint-disable-next-line
		/*
		useEffect runs on any update so when one of the above
		functions runs it updates the component in a loop, the 
		empty array stops that as it defines conditions on when 
		the functions should be run, an empty array only runs the 
		functions once. 
		*/
	}, []);

	const {
		name, 
		avatar_url, 
		location, 
		bio, 
		blog, 
		login,
		html_url,
		followers,
		following,
		public_repos,
		public_gists,
		hireable,
		company
	} = user;
			
		if(loading) return <Spinner />
		return <Fragment> 
				<Link to='/' className='btn btn-light'>
					Back to Search
				</Link>
				{
					hireable ? 	<p className='text-success'>
									This user is currently looking for work
								</p> : 
								<p className='text-danger'>
									This user is not currently looking for work
								</p>
				}
				<div className='card grid-2'>
					<div className='all-center'>
						<img src={avatar_url} className='round-img' style={{width: '150px' }} alt='user avatar'/>
						<h1>{name}</h1>
						<p> Location: { location } </p>
					</div>
					
					<div>
						{bio && (
							<Fragment>
								<h3>Bio</h3>
								<p>{bio}</p>
							</Fragment>
						)}
						<a href={html_url} className='btn btn-dark my-1' target='blank'>
							Visit Github Profile
						</a>
						<ul>
							<li>
								{login && (
									<Fragment>
										<strong>Username: </strong> {login}
									</Fragment>
								)}
							</li>
							
							<li>
								{company && (
									<Fragment>
										<strong>Company: </strong> {company}
									</Fragment>
								)}
							</li>

							<li>
								{blog && (
									<Fragment>
										<strong>Website: </strong> 
										<a target='blank' href={blog}>{blog}</a>
									</Fragment>
								)}
							</li>
						</ul>	
					</div>
				</div>
				<div className='card text-center'>
					<div className='badge badge-primary'>Followers: {followers}</div>
					<div className='badge badge-success'>Following: {following}</div>
					<div className='badge badge-light'>Public Repos: {public_repos}</div>
					<div className='badge badge-dark'>Public Gists: {public_gists}</div>
				</div>
				<Repos repos={repos} />
			</Fragment>
		
	}

export default User;




